<template>
    <div class="food">
        <header>
            <a href="javascript:history.back(-1)">
                <i class="el-icon-arrow-left"></i>
            </a>
            <span>{{title}}</span>
        </header>
        <nav>
            <div @click="classification" :style="{'color':isShowA ? '#3190e8' : '#333'}">

                <span v-if="!isShowA">{{title}}</span>
                <i class="el-icon-caret-bottom" v-if="!isShowA"></i>
                <span v-if="isShowA">分类</span>
                <i class="el-icon-caret-top" v-if="isShowA"></i>
            </div>
            <div @click="sort" :style="{'color':isShowB ? '#3190e8' : '#333'}">
                <span>排序</span>
                <i class="el-icon-caret-bottom" v-if="!isShowB"></i>
                <i class="el-icon-caret-top" v-if="isShowB"></i>
            </div>
            <div @click="screen" :style="{'color':isShowC ? '#3190e8' : '#333'}">
                <span>筛选</span>
                <i class="el-icon-caret-bottom" v-if="!isShowC"></i>
                <i class="el-icon-caret-top" v-if="isShowC"></i>
            </div>
        </nav>
        <el-collapse-transition>
            <div>
                <!--<div class="dd"  v-show="isShowN"></div>-->
                <el-collapse-transition>
                    <div class="classification" v-show="isShowA">
                        <div class="left">
                            <ul>
                                <li><div class="lL"><span>{{allShop1.name}}</span></div><div class="lR" style="right: 0"><span>{{allShop1.count}}</span></div></li>
                            <li v-for="(s,index) in allShop"  @click="addShop(s,index)" :class="{liBackground:changeLeftBackground == index}"><div class="lL"><img :src="'https://fuss10.elemecdn.com/'+s.image_url" class="img"><span>{{s.name}}</span></div><div class="lR"><span>{{s.count}}</span><i class="el-icon-arrow-right"></i></div></li>
                            </ul>
                        </div>
                        <div class="right">
                            <ul>
                                <li v-for="s in sub_categories" @click="addmisite(s)">
                                    <span>{{s.name}}</span>
                                    <span>{{s.count}}</span>
                                </li>
                            </ul>
                        </div>
                    </div>
                </el-collapse-transition>
                <el-collapse-transition>
                    <div class="sort" v-show="isShowB">
                        <ul>
                            <li @click="sort1(4)">
                                <i class="el-icon-sort" style="color: rgb(59, 135, 200)"></i>
                                <p>
                                    <span :style="{'color':indexS==4 ? '#3190e8' : '#333'}">智能排序</span>
                                    <i class="el-icon-check" v-show="indexS == 4 ? true:false"></i>
                                </p>
                            </li>
                            <li @click="sort1(5)">
                                <i class="el-icon-location-outline" style="color:rgb(42, 155, 211);"></i>
                                <p>
                                    <span :style="{'color':indexS==5 ? '#3190e8' : '#333'}">距离最近</span>
                                    <i class="el-icon-check" v-show="indexS == 5 ? true:false"></i>
                                </p>
                            </li>
                            <li @click="sort1(6)">
                                <i class="el-icon-potato-strips" style="color: rgb(240, 115, 115);"></i>
                                <p >
                                    <span :style="{'color':indexS==6 ? '#3190e8' : '#333'}">销量最高</span>
                                    <i class="el-icon-check" v-show="indexS == 6 ? true:false"></i>
                                </p></li>
                            <li @click="sort1(1)">
                                <i style="color: rgb(230, 182, 26)">￥</i>
                                <p >
                                    <span :style="{'color':indexS==1 ? '#3190e8' : '#333'}">起送价最低</span>
                                    <i class="el-icon-check" v-show="indexS == 1 ? true:false"></i>
                                </p></li>
                            <li @click="sort1(2)">
                                <i class="el-icon-timer" style="color: rgb(55, 199, 183);"></i>
                                <p>
                                    <span :style="{'color':indexS==2 ? '#3190e8' : '#333'}">配送速度最快</span>
                                    <i class="el-icon-check" v-show="indexS == 2 ? true:false"></i>
                                </p></li>
                            <li @click="sort1(3)">
                                <i class="el-icon-star-off" style="color: rgb(235, 165, 59);"></i>
                                <p>
                                    <span :style="{'color':indexS==3 ? '#3190e8' : '#333'}">评分最高</span>
                                    <i class="el-icon-check" v-show="indexS == 3 ? true:false"></i>
                                </p>
                            </li>
                        </ul>
                    </div>
                </el-collapse-transition>
                <el-collapse-transition>
                    <div class="screen" v-show="isShowC">
                        <div class="screenC1">
                            <div>配送方式</div>
                            <ul>
                                <li v-for="(f,index) in delivery_modes" @click="isShows = !isShows">
                                    <i class="el-icon-bicycle" style="color: #3190e8" v-if="isShows"></i>
                                    <i class="el-icon-check" style="color: #3190e8" v-if="!isShows"></i>
                                    <span>{{f.text}}</span>
                                </li>
                            </ul>
                        </div>

                        <div class="screenC1" id="screenC1">
                            <div>商家属性（可以多选）</div>
                            <ul>
                                <li v-for="f in activity_attributes" @click="screenZ(f)">
                                    <i class="el-icon-check" style="color: #3190e8" v-if="shaixuanArr[shaixuanArr.indexOf(f.id)]==f.id"></i>
                                    <span class="screenC1_span1" v-if="!(shaixuanArr[shaixuanArr.indexOf(f.id)]==f.id)">{{f.icon_name}}</span>
                                    <span class="screenC1_span2">{{f.name}}</span>
                                </li>
                            </ul>
                        </div>
                        <div class="screenC2" >
                            <div class="filter_button_style" @click="clearS">清空</div>
                            <div class="filter_button_style" @click="screenS">确定<span v-if="indexC">({{indexC}})</span></div>
                        </div>
                    </div>
                </el-collapse-transition>
            </div>
        </el-collapse-transition>
        <section>
            <ul>
                <li v-for="s in shopping" class="sli" @click="toShop(s)">
                    <img :src="'https://elm.cangdu.org/img/'+s.image_path" class="simg">
                    <div class="sli1"><h4><span>品牌</span>{{s.name}}</h4>
                        <ul><li v-for="a in s.supports">
                            <span>{{a.icon_name}}</span>
                        </li></ul></div>
                    <div class="sli2"><el-rate
                            v-model="s.rating"
                            disabled
                            text-color="#ff9900"
                            class="rating"
                    >
                    </el-rate><span class="ratingnumber">{{s.rating}}</span><span class="recent">月销售：{{s.recent_order_num}}单</span><span class="rating_right"><span>{{s.delivery_mode?s.delivery_mode.text:''}}</span><span>{{s.supports[1]?s.supports[1].name:''}}</span></span></div>
                    <div class="sli3"><span>￥{{s.float_minimum_order_amount}}起送 / 配送费约￥{{s.float_delivery_fee}}</span><p>{{s.distance}} / <span>{{s.order_lead_time}}</span></p></div>
                </li>
            </ul>
        </section>
    </div>
</template>

<script>
    export default {
        name: "Food",
        data(){
            return{
                geohash:"",
                latitude:"",
                longitude:"",
                position:"",
                shopping:[],
                masite:[],
                isShow1:true,
                isShow2:false,
                title:"",
                isShowN:false,
                isShowA:false,
                isShowB:false,
                isShowC:false,
                A:0,
                B:0,
                C:0,
                allShop1:[],
                allShop:[],
                sub_categories:[],
                changeLeftBackground:0,
                indexS:0,
                delivery_modes:[],
                isShows:true,
                activity_attributes:[],
                indexC:null,
                shaixuanArr:[],
            }
        },
        created(){
            this.title = this.$route.params.title;
            this.msite = JSON.parse(localStorage.getItem("msite"));
            //获取坐标位置
            this.axios.get("/v2/pois/"+this.msite.geohash).then(res=>{
                this.position = res;
                // console.log(res);
            }).catch(err=>{});

            //获取商铺列表
            this.axios.get("/shopping/restaurants?",{
                params:{
                    latitude:this.msite.latitude,
                    longitude:this.msite.longitude
                }
            }).then(res=>{
                this.shopping = res;
                // console.log(res);
            }).catch(err=>{});

            //获取所有商铺分类
            this.axios.get("/shopping/v2/restaurant/category").then(res=>{
                for(let i = 0;i < res.length; i++){
                    if(i<1){
                        this.allShop1 = res[i];
                    }else{
                        this.allShop.push(res[i]);
                    }
                }
                for(let i = 0;i < res[1].sub_categories.length; i++){
                    if(i>0){
                        this.sub_categories.push(res[1].sub_categories[i]);
                    }
                }
                // console.log(res);
            }).catch(err=>{});
            //配送方式
            this.axios.get("shopping/v1/restaurants/delivery_modes",{
            }).then(res=>{
                // console.log(res);
                this.delivery_modes = res;
            }).catch(err=>{})
            //商家属性
            this.axios.get("shopping/v1/restaurants/activity_attributes",{
            }).then(res=>{
                // console.log(res);
                this.activity_attributes = res;
            }).catch(err=>{})
        },
        methods:{
            classification(){
                this.isShowN = !this.isShowN;
                this.isShowA = !this.isShowA;
                this.isShowB = false;
                this.isShowC = false;
            },
            sort(){
                this.isShowA = false;
                this.isShowB = !this.isShowB;
                this.isShowC = false;
            },
            screen(){
                this.isShowA = false;
                this.isShowB = false;
                this.isShowC = !this.isShowC;
            },
            addShop(s,index){
                this.sub_categories = [];
                for(let i = 0;i<s.sub_categories.length;i++){
                    if(i>0){
                        this.sub_categories.push(s.sub_categories[i]);
                    }
                }
                this.changeLeftBackground = index;
            },
            addmisite(s){
                //获取分类店铺信息
                this.title = s.name;
                this.axios.get("shopping/restaurants?latitude="+this.msite.latitude+'&longitude='+this.msite.longitude+'&restaurant_category_ids[]='+s.id).then(res=>{
                    // console.log(res);
                    this.shopping = res;
                    this.isShowA = false;
                    this.isShowB = false;
                    this.isShowC = false;
                    this.isShowN = false;
                }).catch(err=>{});

            },
            sort1(id){
                this.indexS = id;
                //获取店铺信息
                this.axios.get("shopping/restaurants?",{
                    params:{
                        latitude:this.msite.latitude,
                        longitude:this.msite.longitude,
                        order_by:id
                    }
                }).then(res=>{
                    this.shopping = res;
                    this.isShowA = false;
                    this.isShowB = false;
                    this.isShowC = false;
                    this.isShowN = false;
                    // console.log(res);
                }).catch(err=>{})
            },
            screenZ(f){
                if(this.shaixuanArr.indexOf(f.id)<0){
                    this.shaixuanArr.push(f.id);
                }else{
                    this.shaixuanArr.splice(this.shaixuanArr.indexOf(f.id),1);
                }

                this.indexC = this.shaixuanArr.length;
            },
            toShop(s){
                this.$router.push("/shop");
                localStorage.setItem("shop",JSON.stringify(s));
            },
            clearS(){
                this.shaixuanArr = [];
                this.indexC = "";
            },
            screenS(){
                this.isShows = true;
                this.isShowC = false;
                this.isShowN = false;
                this.axios.get("shopping/restaurants?latitude="+this.masite.latitude+'&longitude='+this.masite.longitude+'&support_ids[]='+this.shaixuanArr).then(res=>{
                    // console.log(res)
                    this.shopping = res;
                }).catch(err=>{});

            }
        }
    }
</script>

